body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
input, label {
  display: block; }

* {
  box-sizing: border-box; }

button {
  display: block;
  margin: 2rem; }

code {
  white-space: pre-wrap;
  background: #d8d8d8;
  color: #151515;
  display: block;
  font-weight: bold;
  width: 90%;
  line-height: 1rem;
  padding: 1rem;
  margin: 0 auto; }

code + p {
  margin: 0;
  padding: 0; }

p code {
  padding: 1rem;
  margin: 0; }

code p {
  padding: 0;
  margin: 0; }

